.headerWrapper{
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;    
    background-color:rgb(14, 87, 136); ;
    justify-content: space-around;
    margin-bottom: 15px;
}
.menuTitle{
    color:rgb(64, 214, 252);
    font-size: 15px;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
    text-decoration: none;
}
.menuTitle.active{
border-bottom: 2px solid black;
}
